//
//Backgrounds
//


//BG pattern dots
.bg-pattern {
  background-image: radial-gradient(circle, currentColor 1px, rgba(0, 0, 0, 0) 1px);
  background-size: 1rem 1rem;
}

//bg-image
//background-image styles
img.bg-image {
  object-fit: cover;
}

.bg-image {
  &:not([class*="position-absolute"]):not([class*="position-relative"]) {
    position: absolute;
  }

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;

  +* {
    position: relative;
    z-index: 1;
  }
}

//Gradient-backgrounds
.bg-gradient-primary {
  background-image: $bg-gradient-primary;
}

.bg-gradient-secondary {
  background-image: $bg-gradient-secondary;
}

.bg-gradient-light {
  background-image: $bg-gradient-light;
}

.bg-gradient-white {
  background-image: $bg-gradient-white;
}

.bg-gradient-dark {
  background-image: $bg-gradient-dark;
}

.bg-gradient-tint {
  background-image: $bg-gradient-tint;
}

//Light(tint) backgrounds
@mixin bg-tint($color: $theme-colors) {
  background-color: tint-color($color, 80%) !important;
}

@each $color,
$value in $theme-colors {
  .bg-tint-#{$color} {
    @include bg-tint($value);
  }
}

//Dark(shade) backgrounds for colors
@mixin bg-shade($color: $theme-colors) {
  background-color: shade-color($color, 50%) !important;
}

@each $color,
$value in $theme-colors {
  .bg-shade-#{$color} {
    @include bg-shade($value);
  }
}

//Hover tint background
@mixin hover-bg-tint($color: $theme-colors) {
  background-color: tint-color($color, 90%) !important;
}

@each $color,
$value in $theme-colors {
  .hover-bg-tint-#{$color} {
    &:hover {
      @include hover-bg-tint($value);
    }
  }
}

@each $color,
$value in $theme-colors {
  .hover-bg-#{$color} {
    transition: all .25s;

    &:hover {
      background-color: ($value) !important;
    }

    &.card {
      &:hover {
        border-color: ($value) !important;
      }
    }
  }
}

.hover-text-white {
  &:hover {
    color: white !important;
  }
}

@each $color,
$value in $theme-colors {
  .bg-blur-#{$color} {
    background-color: rgba($value, .4);
    backdrop-filter: blur(88px);
  }
}

.bg-currentColor {
  background-color: currentColor !important;
}


//Blur gradient background
.bg-gradient-blur {
  background-color: $primary;
  background-image: linear-gradient(262.83deg, $gradient-alt 8.71%, $primary 89.74%);
  opacity: 0.5;
  -webkit-filter: blur(200px);
  filter: blur(200px);
}

//Overlay background
.bg-overlay-gradient {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden;

  &::before,
  &::after {
    content: "";
    position: absolute;
    z-index: -1;
  }

  &::after {
    content: "";
    top: 0;
    background: radial-gradient(50% 50% at 50% 50%, tint-color($primary, 90%) 0, rgba(204, 244, 100, 0) 78.74%);
    display: block;
    position: absolute;
    width: 118vh;
    height: 118vh;
    right: 20vh;
    transform: translate(50%, -50%);
  }

  &::before {
    background: radial-gradient(50% 50% at 50% 50%, tint-color($gradient-alt, 90%) 0, rgba(248, 205, 209, 0) 78.74%);
    position: absolute;
    display: block;
    content: "";
    top: -60%;
    left: -90%;
    width: 200%;
    padding-bottom: 200%;
  }
}