//Accordion style
.accordion-button {
    font-weight: $headings-font-weight-sm;
}

//custo underline-accordion
.accordion.accordion-custom {
    .accordion-item {
        border: 0px;
    }

    .accordion-body {
        padding-top: 0;
    }


    .accordion-button {
        box-shadow: $box-shadow;
        transition: all .25s;
        position: relative;
        z-index: 1;
        overflow: hidden;
        background-color: $white;
        border-radius: $border-radius;
        &::after {
            background-image: none;
            font-family: bootstrap-icons !important;
            -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
            content: "\f64d";
            font-size: $font-size-base;
            color: $primary;
        }
    }
    .accordion-collapse,.accordion-button:not(.collapsed){
        background-color: tint-color($primary, 90%);
    }
    .accordion-collapse{
        border-bottom-left-radius: $accordion-border-radius;
        border-bottom-right-radius: $accordion-border-radius;
    }
    .accordion-button:not(.collapsed) {
        box-shadow: none;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        &::after {
            background-image: none;
            -webkit-transform: rotate(0);
            transform: rotate(0);
            content: "\f63b";
            color: $primary;
        }
    }
}