.dropdown-item {
    font-family: $dropdown-font-family;
    transition: $transition-base;
    text-transform: $dropdown-text-transform;
    letter-spacing: $dropdown-letter-spacing;
    font-size: $dropdown-font-size;
    font-weight: $dropdown-font-weight;
}

.text-white,
.bg-dark,
.dropdown-menu-dark {
    .dropdown-divider {
        opacity: .2;
    }
}

.dropdown-menu {
    .dropdown-header {
        text-transform: $dropdown-text-transform;
    }
}


//Dropdown-menu no collapse items in navbar
.navbar-no-collapse-items {
    .dropdown-menu.dropdown-menu-xs {
        min-width: 300px;
        width: 300px;
    }
}

//Dropdown Mega Menu
@include media-breakpoint-up(lg) {
    .navbar .dropdown-menu-end {
        right:0;
        left: auto;
    }

    .navbar .dropdown-menu {
        display: block;
        visibility: hidden;
        transform: translateY(16px);
        opacity: 0;
        &.show {
            transition: opacity .15s, transform .25s cubic-bezier(.25, .5, .75, 1);
            -webkit-transform: none;
            transform: none;
            opacity: 1;
            visibility: visible;
        }
    }

    .dropdown-menu-fw {
        width:auto;
        max-width: none;
        margin-left: .75rem;
        margin-right: .75rem;
        right: 0;
        &.dropdown-menu[data-bs-popper]{
            right:0;
        }
    }

    .dropdown-menu-md {
        width: 720px;
    }

    .dropdown-menu-sm {
        width: 540px;
    }

    .navbar-no-collapse-items {
        .dropdown-menu.dropdown-menu-xs {
            min-width: 360px;
            width: 360px;
        }
    }

    .dropdown-menu-xs {
        width: 360px;
    }
}

@include media-breakpoint-up(xl) {
    .navbar {
        .dropdown-menu-lg {
            width: auto;
            max-width: none;

            &.dropdown-menu-end[data-bs-popper]{
                right:0;
                margin-right:.75rem;
            }
        }

        .dropdown-menu-md {
            width: 720px;
        }

        .dropdown-menu-sm {
            width: 540px;
        }

        .dropdown-menu-xs {
            width: 340px;
        }
    }
}

@include media-breakpoint-down(lg) {
    .navbar.navbar-expand-lg {
        .dropdown-menu {
            box-shadow: none;
            border-left: 1px solid $border-color;
            padding-left: 0;
            border-radius: 0;
        }

        .dropend,
        .dropstart {
            padding-left: 1rem;

            .dropdown-menu {
                opacity: 1;
                display: block;
                position: static;
                padding-left: 0;
                padding-top: 0;
                padding-bottom: 0;
            }
        }

        .nav-item.dropdown {
            position: static;
        }

        .navbar-no-collapse-items {
            .dropdown-menu {
                min-width: 100%;
                max-width: 100%;
                width: 100%;
            }
        }
    }
}

.dropend .dropdown-menu {
    top: 0;
    right: auto;
    left: 100%;
    margin-top: 0;
    margin-left: 0;
}

.dropstart .dropdown-menu {
    top: 0;
    right: 100%;
    left: auto;
    margin-top: 0;
    margin-left: 0;
}

.dropdown-menu.dropdown-menu-xxs {
    max-width: 160px;
    min-width: 160px;
}